.z-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.z-container .z-navbar {
  display: flex;
  justify-content: space-between;
  height: 3.5rem;
  box-sizing: border-box;
  border-bottom: 1px solid #eaecef;
  padding: 0.8rem 1.5rem;
  color: #2c3e50;
}
.z-container .z-navbar .z-title {
  font-size: 1.2rem;
  font-weight: 600;
}
.z-container .z-navbar .z-links {
  display: flex;
  line-height: 1.9rem;
}
.z-container .z-navbar .z-links .z-nav-item:first-child {
  margin-left: 0;
}
.z-container .z-navbar .z-links .z-nav-item {
  margin-left: 1.5rem;
}
.z-container .z-navbar .z-links .z-nav-item a {
  font-weight: 500;
  color: #2c3e50;
}
.z-container .z-page {
  flex: 1 1;
  overflow: auto;
  text-align: center;
  padding: 1rem;
}
.z-container .z-page .z-article-list ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.z-container .z-page .z-article-list li {
  margin-bottom: 0.5rem;
}
.z-container .z-page .z-article-list a {
  font-size: 1.2rem;
  font-weight: 600;
  line-height: 1.2;
  color: #2c3e50;
}
.z-container .z-page .z-article-list a:hover {
  color: #2486ff;
}
